import React from "react";

import { Carousel } from "react-bootstrap";
import { slideInfo } from "./slide-info";

import "./App.css";

function App() {
  const slideList = Object.keys(slideInfo);

  return (
    <div className="App">
      <Carousel indicators={false} interval={null}>
        {slideList.map((slide, sIdx) => {
          const { img, heading, captions } = slideInfo[slide];

          return (
            <Carousel.Item key={sIdx}>
              {img}
              <Carousel.Caption>
                <h5>{heading}</h5>
                {captions.map((caption, cIdx) => (
                  <li key={cIdx}>{caption}</li>
                ))}
              </Carousel.Caption>
            </Carousel.Item>
          );
        })}
      </Carousel>
    </div>
  );
}

export default App;
